import { createRouter, createWebHistory } from 'vue-router'
import Home from "../views/Home.vue"
import Destination from "../views/Destination.vue"
import Moon from "../views/Moon.vue"
import Titan from "../views/Titan.vue"
import Europa from "../views/Europa.vue"
import Crew from "../views/Crew.vue"
import Douglas from "../views/Douglas.vue"
import Mark from "../views/Mark.vue"
import Victor from "../views/Victor.vue"
import Technology from "../views/Technology.vue"
import Capsule from "../views/Capsule.vue"
import Spacesport from "../views/Spacesport.vue"


const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    // ---DESTINATION----
    {
        path: '/destination/mars',
        name: 'mars',
        component: Destination
    },
    {
        path: '/destination/moon',
        name: 'moon',
        component: Moon
    },
    {
        path: '/destination/europa',
        name: 'europa',
        component: Europa
    },
    {
        path: '/destination/titan',
        name: 'titan',
        component: Titan
    },
    // -----CREW---
    {
        path: '/crew',
        name: 'crew',
        component: Crew
    },
    {
        path: '/crew/douglas',
        name: 'douglas',
        component: Douglas
    },
    {
        path: '/crew/mark',
        name: 'mark',
        component: Mark
    },
    {
        path: '/crew/victor',
        name: 'victor',
        component: Victor
    },
    // -----TECHNOLOGY----
    {
        path: '/technology',
        name: 'technology',
        component: Technology
    },
    {
        path: '/technology/capsule',
        name: 'deux',
        component: Capsule
    },
    {
        path: '/technology/spacesport',
        name: 'spacesport',
        component: Spacesport
    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router